/* Universal */

body {
	background-color: violet;
}

#nbList {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: row;
	    flex-direction: row;
	padding-right: 2.1em;
}

li a {
	padding-left: 0.5em;
	padding-right: 0.5em;
	color: black;
	list-style-type: none;
}

.nB0Item {
	list-style-type: none;
}

#header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	text-decoration: underline !important;
	
}

textarea {
	resize: none;
}

#footer {
	text-align: center;
}

a {
	color: white;
}

::-webkit-scrollbar {
    width: 0em;
    height: 0em;
    background: transparent;
}

/* app.js */

#contain {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;

}

#mdpDiv {
	height: 43em;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin-top: -1em;
}

/* Also included in markDownOutput.js */

.markDownElements {
	position: relative;
	height: 100%;
	width: 35em;
	overflow: none;
	border-style: solid;
	border-width: 0.2em;
	border-color: black;
	border-radius: 1.5em;
	background-color: blue;
	background-color: rgba(42, 109, 131, 1);
}

/* app.js */

#inputHeader {
	position: relative;
	text-align: center;
	text-decoration: underline !important;

}

#editorDiv {
	overflow: scroll;
}

#editor {
	border: none;
	position: relative;
	margin-bottom: 4em;
	height: 88%;
	width: 99%;
	font-size: 0.9em;
	background-color: blue;
	background-color: rgba(42, 109, 131, 1);
}

textarea:focus {
	outline: 0;
}

/* markDownOutput.js */

#outputHeader {
	position: relative;
	text-align: center;
	text-decoration: underline !important;
}

#preview {
	position: relative;
	text-decoration: none;
	overflow: scroll;
}

/* about.js */

#abtText {
	text-align: center;
	padding-bottom: 36%
}

#abtFooter {
	position: relative;
    text-align: center;
}




